.container {
	width        : calc(100% - 2 * var(--component-padding));
	margin-left  : auto;
	margin-right : auto;
}

.grid,
.flex,
.inline-flex,
[class^='flex\@'],
[class*=' flex@'],
[class^='inline-flex\@'],
[class*=' inline-flex@'] {
	--gap   : 0px;
	--gap-x : var(--gap);
	--gap-y : var(--gap);
	gap     : var(--gap-y) var(--gap-x);
}

.grid > *,
.flex > *,
.inline-flex > *,
[class^='flex\@'] > *,
[class*=' flex@'] > *,
[class^='inline-flex\@'] > *,
[class*=' inline-flex@'] > * {
	--sub-gap   : 0px;
	--sub-gap-x : var(--sub-gap);
	--sub-gap-y : var(--sub-gap);
}

.grid {
	--grid-columns : 12;
	display        : flex;
	flex-wrap      : wrap;
}

.grid > * {
	flex-basis : 100%;
	max-width  : 100%;
	min-width  : 0;
}

/* #region (Safari < 14.1 fallback) */
@media not all and (min-resolution : 0.001dpcm) {
	@supports not (translate: none) {
		.grid,
		.flex[class*='gap-'],
		.inline-flex[class*='gap-'] {
			gap           : 0px;
			margin-bottom : calc(-1 * var(--gap-y));
			margin-left   : calc(-1 * var(--gap-x));
		}
		.grid > *,
		.flex[class*='gap-'] > *,
		.inline-flex[class*='gap-'] > * {
			margin-bottom : var(--sub-gap-y);
		}
		
		.grid {
			--offset          : var(--gap-x);
			--gap-modifier    : 0;
			--offset-modifier : 1;
		}
		.grid > * {
			margin-left : var(--offset);
		}
		
		.flex[class*='gap-'] > *,
		.inline-flex[class*='gap-'] > * {
			margin-left : var(--sub-gap-x);
		}
	}
}

