:root {
	--list-space-y                : 0.375em;
	--list-offset                 : 1em;
	--list-line-height-multiplier : 1;
}

.list,
.text-component .list {
	padding-left : 0;
	list-style   : none;
}

.list ul,
.list ol,
.text-component .list ul,
.text-component .list ol {
	list-style   : none;
	margin       : 0;
	margin-top   : calc(var(--list-space-y) / 2 * var(--text-space-y-multiplier, 1));
	padding-top  : calc(var(--list-space-y) / 2 * var(--text-space-y-multiplier, 1));
	padding-left : var(--list-offset);
}

.list li,
.text-component .list li {
	padding-bottom : calc(
					var(--list-space-y) / 2 * var(--text-space-y-multiplier, 1)
	);
	margin-bottom  : calc(
					var(--list-space-y) / 2 * var(--text-space-y-multiplier, 1)
	);
	line-height    : calc(
					var(--body-line-height) * var(--list-line-height-multiplier)
	);
}

.list > li:last-child,
.list ul > li:last-child,
.list ol > li:last-child,
.text-component .list > li:last-child,
.text-component .list ul > li:last-child,
.text-component .list ol > li:last-child {
	margin-bottom : 0;
}

.list:not(.list--border) > li:last-child,
.list ul > li:last-child,
.list ol > li:last-child,
.text-component .list:not(.list--border) > li:last-child,
.text-component .list ul > li:last-child,
.text-component .list ol > li:last-child {
	padding-bottom : 0;
}

/* #region (ul + ol) */
.list--ul,
.text-component .list--ul,
.list--ol,
.text-component .list--ol {
	--list-offset : calc(var(--list-bullet-size) + var(--list-bullet-margin-right));
}

.list--ul ul,
.list--ul ol,
.text-component .list--ul ul,
.text-component .list--ul ol,
.list--ol ul,
.list--ol ol,
.text-component .list--ol ul,
.text-component .list--ol ol {
	padding-left : 0;
}

.list--ul li,
.text-component .list--ul li,
.list--ol li,
.text-component .list--ol li {
	padding-left : var(--list-offset);
}

.list--ul li::before,
.text-component .list--ul li::before,
.list--ol li::before,
.text-component .list--ol li::before {
	display         : inline-flex;
	justify-content : center;
	align-items     : center;
	width           : var(--list-bullet-size);
	height          : var(--list-bullet-size);
	vertical-align  : middle;
	position        : relative;
	top             : -0.1em;
	left            : calc(var(--list-bullet-margin-right) * -1);
	margin-left     : calc(var(--list-bullet-size) * -1);
}

.list--ul,
.text-component .list--ul {
	--list-bullet-size         : 7px;
	--list-bullet-margin-right : 12px;
}

.list--ul > li::before,
.text-component .list--ul > li::before {
	content          : '';
	border-radius    : 50%;
	color            : hsla(
									var(--color-contrast-higher-h),
									var(--color-contrast-higher-s),
									var(--color-contrast-higher-l),
									0.1
	);
	background-color : currentColor;
}

.list--ul ul li::before,
.text-component .list--ul ul li::before {
	background-color : transparent;
	box-shadow       : inset 0 0 0 2px currentColor;
}

.list--ol,
.text-component .list--ol {
	--list-bullet-size         : 26px;
	--list-bullet-margin-right : 6px;
	--list-bullet-font-size    : 14px;
	counter-reset              : list-items;
}

.list--ol > li,
.text-component .list--ol > li {
	counter-increment : list-items;
}

.list--ol ol,
.text-component .list--ol ol {
	counter-reset : list-items;
}

.list--ol > li::before,
.text-component .list--ol > li::before {
	content          : counter(list-items);
	font-size        : var(--list-bullet-font-size, 14px);
	background-color : hsla(
									var(--color-contrast-higher-h),
									var(--color-contrast-higher-s),
									var(--color-contrast-higher-l),
									0.075
	);
	color            : var(--color-contrast-higher);
	line-height      : 1;
	border-radius    : 50%;
}

.list--ol ol > li::before,
.text-component .list--ol ol > li::before {
	background-color : transparent;
	box-shadow       : inset 0 0 0 2px hsla(
									var(--color-contrast-higher-h),
									var(--color-contrast-higher-s),
									var(--color-contrast-higher-l),
									0.075
	);
}

/* #endregion */
/* #region (border) */
.list--border li:not(:last-child),
.text-component .list--border li:not(:last-child) {
	border-bottom : 1px solid var(--color-contrast-lower);
}

.list--border ul,
.list--border ol,
.text-component .list--border ul,
.text-component .list--border ol {
	border-top : 1px solid var(--color-contrast-lower);
}

/* #endregion */
/* #region (icons) */
.list--icons,
.text-component .list--icons {
	--list-bullet-size         : 24px;
	--list-bullet-margin-right : 8px;
	--list-offset              : calc(var(--list-bullet-size) + var(--list-bullet-margin-right));
}

.list--icons ul,
.list--icons ol,
.text-component .list--icons ul,
.text-component .list--icons ol {
	padding-left : var(--list-offset);
}

.list__icon {
	position     : relative;
	width        : var(--list-bullet-size);
	height       : var(--list-bullet-size);
	margin-right : var(--list-bullet-margin-right);
}

.list__icon:not(.top-0) {
	top : calc(
					(1em * var(--body-line-height) * var(--list-line-height-multiplier) -
					var(--list-bullet-size)) / 2
	);
}

// 💬 A look at CodyFrame's mixins
// 🔗 https://codyhouse.co/ds/docs/framework/mixins

// --------------------------------

// Typography

// --------------------------------

// edit font rendering -> tip: use for light text on dark backgrounds
@mixin fontSmooth {
	-webkit-font-smoothing  : antialiased;
	-moz-osx-font-smoothing : grayscale;
}

// crop top space on text elements - caused by line height
@mixin lhCrop($line-height, $capital-letter: 1) {
	&::before {
		content    : '';
		display    : block;
		height     : 0;
		width      : 0;
		margin-top : calc((#{$capital-letter} - #{$line-height}) * 0.5em);
	}
}

// edit text unit on a component level
@mixin textUnit($text-unit) {
	--text-unit : #{$text-unit};
	font-size   : var(--text-unit);
}

// --------------------------------

// Spacing

// --------------------------------

// edit space unit on a component level
@mixin spaceUnit($space-unit) {
	--space-unit : #{$space-unit};
}

// --------------------------------

// Reset

// --------------------------------

// reset user agent style
@mixin reset {
	background-color : transparent;
	padding          : 0;
	border           : 0;
	border-radius    : 0;
	color            : inherit;
	line-height      : inherit;
	appearance       : none;
}

// --------------------------------

// Colors

// --------------------------------

// define HSL color variable
@mixin defineColorHSL($color, $hue, $saturation, $lightness) {
	#{$color}   : unquote('hsl(#{$hue}, #{$saturation}, #{$lightness})');
	#{$color}-h : #{$hue};
	#{$color}-s : #{$saturation};
	#{$color}-l : #{$lightness};
}

// return color with different opacity value
@function alpha($color, $opacity) {
	$color : str-replace($color, 'var(');
	$color : str-replace($color, ')');
	$color-h : var(#{$color + '-h'});
	$color-s : var(#{$color + '-s'});
	$color-l : var(#{$color + '-l'});
	@return hsla($color-h, $color-s, $color-l, $opacity);
}

// return color with different lightness value
@function lightness($color, $lightnessMultiplier) {
	$color : str-replace($color, 'var(');
	$color : str-replace($color, ')');
	$color-h : var(#{$color + '-h'});
	$color-s : var(#{$color + '-s'});
	$color-l : var(#{$color + '-l'});
	@return hsl($color-h, $color-s, calc(#{$color-l} * #{$lightnessMultiplier}));
}

// modify color HSLA values
@function adjustHSLA(
	$color,
	$hueMultiplier: 1,
	$saturationMultiplier: 1,
	$lightnessMultiplier: 1,
	$opacity: 1
) {
	$color : str-replace($color, 'var(');
	$color : str-replace($color, ')');
	$color-h : var(#{$color + '-h'});
	$color-s : var(#{$color + '-s'});
	$color-l : var(#{$color + '-l'});
	@return hsla(
									calc(#{$color-h} * #{$hueMultiplier}),
									calc(#{$color-s} * #{$saturationMultiplier}),
									calc(#{$color-l} * #{$lightnessMultiplier}),
									$opacity
	);
}

// replace substring with another string
// credits: https://css-tricks.com/snippets/sass/str-replace-function/
@function str-replace($string, $search, $replace: '') {
	$index : str-index($string, $search);
	@if $index {
		@return str-slice($string, 1, $index - 1) + $replace +
			str-replace(
				str-slice($string, $index + str-length($search)),
				$search,
				$replace
			);
	}
	@return $string;
}

// --------------------------------

// Accessibility

// --------------------------------

// hide - content made available only to screen readers
@mixin srHide {
	position  : absolute;
	clip      : rect(1px, 1px, 1px, 1px);
	clip-path : inset(50%);
}

// show
@mixin srShow {
	position  : static;
	clip      : auto;
	clip-path : none;
}

// --------------------------------

// Grid & Layout

// --------------------------------

$grid-columns : 12 !default;

// used to round width to a number with 2 decimal places - used for IE fallback
@function round-width($i) {
	$width : floor(100% * $i * 100 / $grid-columns) / 100;
	@return $width;
}

// used to create the auto-size columns of the grid system (.col class)
@mixin autoSizedColumn {
	flex-grow  : 1;
	flex-basis : 0;
	max-width  : 100%;
}

// CSS Grid Layout fallback
@mixin gridFallback($gap: null) {
	display   : flex;
	flex-wrap : wrap;
	@if ($gap == null) {
		$gap : var(--space-md);
	}
	
	margin    : calc(#{$gap} * -1) 0 0 calc(#{$gap} * -1);
	
	> * {
		padding         : #{$gap} 0 0 #{$gap};
		background-clip : content-box;
	}
	
	@supports (grid-area: auto) {
		margin : 0;
		
		> * {
			padding   : 0;
			max-width : none !important;
		}
	}
}

@mixin column($width) {
	@if unit($width) != '%' {
		$width : $width * 100%;
	}
	flex-basis : #{$width};
	max-width  : #{$width};
}

// --------------------------------

// CSS Triangle

// --------------------------------

@mixin triangle($direction: up, $width: 12px, $color: red) {
	width  : 0;
	height : 0;
	border : $width solid transparent;
	
	@if ($direction == left) {
		border-right-color  : $color;
	} @else if ($direction == right) {
		border-left-color   : $color;
	} @else if ($direction == down) {
		border-top-color    : $color;
	} @else {
		border-bottom-color : $color;
	}
}

// --------------------------------

// Breakpoints

// --------------------------------

$breakpoints  : (
				xs: 32rem,
	// ~512px
				sm: 48rem,
	// ~768px
				md: 64rem,
	// ~1024px
				lg: 80rem,
	// ~1280px
				xl: 90rem // ~1440px
) !default;

@mixin breakpoint($breakpoint, $logic: false) {
	@if ($logic) {
		@media #{$logic} and (min-width : map-get($map : $breakpoints, $key : $breakpoint)) {
			@content;
		}
	} @else {
		@media (min-width : map-get($map : $breakpoints, $key : $breakpoint)) {
			@content;
		}
	}
}

.article {
	--heading-line-height     : 1.2;
	--body-line-height        : 1.58;
	--line-height-multiplier  : 1; // modify line-height of all text elements
	--text-space-y-multiplier : 1.2; // control vertical spacing
}

article {
	@extend .article;
	@extend .text-component;
}

main {
	@extend .container;
	@extend .max-width-sm;
	@extend .padding-y-lg;
}
